<script lang="ts">
  import { onMount } from "svelte";
  import SvelteSeo from "svelte-seo";
  import { triggerDrivePortfolio } from "~/utils/functions";
  import { typePortfolioPage } from "~/utils/constants";
  import { chainList, chainMoveList, chainSolList } from "~/lib/chains";
  import { wait } from "~/utils/index";
  import {
    tab,
    wallet,
    chain,
    typeWallet,
    triggerUpdateBundle,
    totalTokens,
    totalNfts,
    totalAirdrops,
    totalPositions,
    unrealizedProfit,
    realizedProfit,
    pastProfit,
  } from "~/store";
  import mixpanel from "mixpanel-browser";
  import { nimbus } from "~/lib/network";
  import {
    createQuery,
    createQueries,
    useQueryClient,
    QueryCache,
  } from "@tanstack/svelte-query";
  import { AnimateSharedLayout, Motion } from "svelte-motion";
  import {
    handleValidateAddress,
    getHoldingToken,
    getOverview,
    getHoldingNFT,
  } from "~/lib/queryAPI";
  import dayjs from "dayjs";
  import "dayjs/locale/en";
  import "dayjs/locale/vi";
  import relativeTime from "dayjs/plugin/relativeTime";
  dayjs.extend(relativeTime);
  import { t } from "~/lib/i18n";
  import { isNimbusVersion } from "~/utils/constants";

  import LoadingPremium from "~/components/LoadingPremium.svelte";
  import AddressManagement from "~/components/AddressManagement.svelte";
  import Overview from "~/UI/Portfolio/Overview.svelte";
  import Testimonial from "~/UI/Testimonial/Testimonial.svelte";
  import Charts from "~/UI/Portfolio/Charts.svelte";
  import Holding from "~/UI/Portfolio/Holding.svelte";
  import PerformanceSummary from "~/UI/Portfolio/PerformanceSummary.svelte";
  import Defi from "~/UI/Portfolio/Defi.svelte";
  import AirdropsAndPointsTracker from "~/UI/Portfolio/AirdropsAndPointsTracker.svelte";
  import History from "~/UI/Portfolio/History.svelte";

  import Reload from "~/assets/reload.svg";

  const portfolioStepTours = [
    {
      element: ".wellcome-portfolio",
      popover: {
        title: "Welcome to our portfolio tools 🤩",
        description:
          "Allow me to guide you to easily access our application with the following main functions",
      },
    },
    {
      element: "#view-the-holding-token",
      popover: {
        title: "Show all Tokens you are holding 🟡",
        description: "Tracking all your Tokens right here",
      },
    },
    {
      element: "#view-the-nft",
      popover: {
        title: "Show all NFTs you are holding 🖼️",
        description: "Tracking all your NFTs right here",
      },
    },
    {
      element: "#view-the-defi",
      popover: {
        title: "Show all DeFi Positions you have 💵",
        description: "Tracking all your DeFi Positions right here",
      },
    },
    {
      element: "#view-the-points-airdrop",
      popover: {
        title: "Show all Airdrops and Points you have 📦",
        description: "Tracking all your Airdrops and Points you got right here",
      },
    },
    {
      element: "#view-the-summary",
      popover: {
        title: "See our Summary about your portfolio 📊",
        description: "We give you the overview about your portfolio",
      },
    },
    // {
    //   element: "#view-the-history",
    //   popover: {
    //     title: "Show all History on you wallet 🧾",
    //     description: "We collect all transactions history you have been traded",
    //   },
    // },
    {
      element: ".view-the-chart-allocation",
      popover: {
        title: "Track your portfolio Allocation 📊",
        description: "View your Portfolio diversify",
      },
    },
    // {
    //   element: ".view-the-chart-performance",
    //   popover: {
    //     title: "Track your portfolio Performance 📊",
    //     description: "Compare your investment with Bitcoin or Ethereum",
    //   },
    // },
    {
      element: ".view-pnl",
      popover: {
        title: "Your profit and loss, in every asset 💰",
        description:
          "With Profit and Loss Calculation, you have the power to make smarter investment choices",
      },
    },
  ];

  const queryClient = useQueryClient();

  let enabledFetchAllData = false;
  let isErrorAllData = false;
  let isLoadingSync = false;
  let loading = false;
  let dataUpdatedTime: any;
  let syncMsg = "";

  let selectedDataPieChart: any = [];
  let selectedTokenHolding = {
    data: [],
    select: [],
  };

  $: chainListQueries =
    $typeWallet?.length !== 0 && $typeWallet === "EVM"
      ? chainList.slice(1)?.map((item) => item.value)
      : $typeWallet?.length !== 0 && $typeWallet === "MOVE"
        ? chainMoveList.slice(1)?.map((item) => item.value)
        : $typeWallet?.length !== 0 && $typeWallet === "SOL"
          ? chainSolList.slice(1)?.map((item) => item.value)
          : [chainSolList[0]?.value];

  $: queryValidate = createQuery({
    queryKey: ["validate", $wallet],
    queryFn: () => handleValidateAddress($wallet),
    retry: false,
    enabled: Boolean($wallet && $wallet?.length !== 0),
  });

  const queryCache = new QueryCache();

  const getSync = async () => {
    try {
      const validateAccount = $queryValidate.data;
      await nimbus.get(
        `/v2/address/${$wallet}/sync?chain=${
          validateAccount?.type === "BUNDLE" ? "" : validateAccount?.type
        }`,
      );
    } catch (e) {
      console.error("e: ", e);
    }
  };

  const getSyncStatus = async () => {
    try {
      const validateAccount = $queryValidate.data;
      const response: any = await nimbus
        .get(
          `/v2/address/${$wallet}/sync-status?chain=${
            validateAccount?.type === "BUNDLE" ? "" : validateAccount?.type
          }`,
        )
        .then((response) => response);
      dataUpdatedTime = response?.data?.lastSync;
      return response;
    } catch (e) {
      console.error("e: ", e);
    }
  };

  const handleSync = async () => {
    console.log("Going to full sync");
    await getSync();

    if ($tab === "token") {
      queryClient.invalidateQueries(["overview"]);
      queryClient.invalidateQueries(["vaults"]);
      if ($chain !== "ALL") {
        queryClient?.invalidateQueries(["token-holding-each-chain"]);
      } else {
        queryClient.invalidateQueries(["token-holding"]);
      }
    }

    if ($tab === "nft") {
      queryClient.invalidateQueries(["nft-holding"]);
    }

    if ($tab === "defi") {
      queryClient.invalidateQueries(["positions"]);
    }

    if ($tab === "airdrops") {
      queryClient.invalidateQueries(["airdrops"]);
      queryClient.invalidateQueries(["points"]);
    }

    if ($tab === "summary") {
      queryClient.invalidateQueries(["nft-holding"]);
      if ($chain !== "ALL") {
        queryClient?.invalidateQueries(["token-holding-each-chain"]);
      } else {
        queryClient.invalidateQueries(["token-holding"]);
      }
      queryClient.invalidateQueries(["airdrops"]);
      queryClient.invalidateQueries(["points"]);
      queryClient.invalidateQueries(["positions"]);
    }

    if ($tab === "history") {
      if ($chain !== "ALL") {
        queryClient?.invalidateQueries(["token-holding-each-chain"]);
      } else {
        queryClient.invalidateQueries(["token-holding"]);
      }
    }
  };

  const handleGetAllData = async (type: string) => {
    isLoadingSync = false;
    enabledFetchAllData = false;

    try {
      let syncStatus: any = await getSyncStatus();

      // sync data again
      if (type === "reload" || !syncStatus?.data?.lastSync) {
        await handleSync();
      }

      // already sync data from db
      if (syncStatus?.data?.lastSync) {
        console.log("start load data (already sync)");
        enabledFetchAllData = true;
        if (
          !$queryOverview.isError &&
          !$queryTokenHolding.isError &&
          !$queryAllTokenHolding.some((item) => item.isError === true) &&
          !$queryNftHolding.isError &&
          !$queryAllNftHolding.some((item) => item.isError === true)
        ) {
          syncMsg = "";
          isLoadingSync = false;
          isErrorAllData = false;
        } else {
          isErrorAllData = true;
        }
      }

      // check data from db
      if (!syncStatus?.data?.lastSync) {
        if (syncStatus?.data?.canWait) {
          syncMsg = syncStatus?.data?.error;
          isLoadingSync = true;
          // keep call api /sync-status until we can not wait
          while (true) {
            if (syncStatus?.data?.lastSync) {
              console.log("start load data (newest sync)");
              enabledFetchAllData = true;
              if (
                !$queryOverview.isError &&
                !$queryTokenHolding.isError &&
                !$queryAllTokenHolding.some((item) => item.isError === true) &&
                !$queryNftHolding.isError &&
                !$queryAllNftHolding.some((item) => item.isError === true)
              ) {
                syncMsg = "";
                isLoadingSync = false;
                isErrorAllData = false;
              } else {
                isErrorAllData = true;
              }
              break;
            } else {
              isLoadingSync = true;
              await wait(5000);
              syncStatus = await getSyncStatus();
            }
          }
        }

        if (!syncStatus?.data?.canWait) {
          // Cut call when we can not wait
          syncMsg = syncStatus?.data?.error;
          isLoadingSync = false;
          isErrorAllData = true;
        }
      }
    } catch (e) {
      console.error("error: ", e);
      isLoadingSync = false;
      isErrorAllData = true;
    }
  };

  //// OVERVIEW
  $: queryOverview = createQuery({
    queryKey: ["overview", $wallet, $chain],
    queryFn: () => getOverview($wallet, $chain, $queryValidate.data),
    enabled: Boolean(
      enabledFetchAllData &&
        $wallet &&
        $wallet?.length !== 0 &&
        !$queryValidate.isFetching &&
        $tab === "token",
    ),
  });

  //// NFT HOLDING
  $: queryNftHolding = createQuery({
    queryKey: ["nft-holding", $wallet, $chain],
    queryFn: () => getHoldingNFT($wallet, $chain, $queryValidate.data),
    enabled: Boolean(
      enabledFetchAllData &&
        $wallet &&
        $wallet?.length !== 0 &&
        !$queryValidate.isFetching &&
        $chain.length !== 0 &&
        $chain !== "ALL" &&
        $tab.length !== 0 &&
        $tab === "nft",
    ),
  });

  $: queryAllNftHolding = createQueries(
    chainListQueries?.map((item: any) => {
      return {
        queryKey: ["nft-holding", $wallet, $chain, item],
        queryFn: () => getHoldingNFT($wallet, item, $queryValidate.data),
        enabled: Boolean(
          enabledFetchAllData &&
            $wallet &&
            $wallet?.length !== 0 &&
            !$queryValidate.isFetching &&
            $chain.length !== 0 &&
            $chain === "ALL" &&
            $tab.length !== 0 &&
            $tab === "nft",
        ),
      };
    }),
  );

  //// TOKEN HOLDING
  $: queryTokenHolding = createQuery({
    queryKey: ["token-holding-each-chain", $wallet, $chain],
    queryFn: () => getHoldingToken($wallet, $chain, $queryValidate.data),
    enabled: Boolean(
      enabledFetchAllData &&
        $wallet &&
        $wallet?.length !== 0 &&
        !$queryValidate.isFetching &&
        $chain.length !== 0 &&
        $chain !== "ALL" &&
        $tab.length !== 0 &&
        $tab === "token",
    ),
  });

  $: queryAllTokenHolding = createQueries(
    chainListQueries?.map((item: any) => {
      return {
        queryKey: ["token-holding", $wallet, $chain, item],
        queryFn: () => getHoldingToken($wallet, item, $queryValidate.data),
        enabled: Boolean(
          enabledFetchAllData &&
            $wallet &&
            $wallet?.length !== 0 &&
            !$queryValidate.isFetching &&
            $chain.length !== 0 &&
            $chain === "ALL" &&
            $tab.length !== 0 &&
            $tab === "token",
        ),
      };
    }),
  );

  const handleSelectedTableTokenHolding = (
    data: any,
    selectDatPieChart: any,
  ) => {
    if (data?.data && data?.data?.length !== 0 && $typeWallet === "BUNDLE") {
      selectedTokenHolding = data;
      selectedDataPieChart = selectDatPieChart;
    }
  };

  onMount(() => {
    mixpanel.track("portfolio_page", {
      address: $wallet,
      chain: $chain,
    });
  });

  $: {
    if ($wallet || $chain) {
      if (
        $wallet?.length !== 0 &&
        $chain?.length !== 0 &&
        !$queryValidate.isFetching
      ) {
        selectedTokenHolding = {
          data: [],
          select: [],
        };
        selectedDataPieChart = [];

        isErrorAllData = false;
        isLoadingSync = false;
        enabledFetchAllData = false;

        totalTokens.update((n) => (n = 0));
        totalNfts.update((n) => (n = 0));
        totalAirdrops.update((n) => (n = 0));
        totalPositions.update((n) => (n = 0));
        unrealizedProfit.update((n) => (n = 0));
        realizedProfit.update((n) => (n = 0));

        enabledFetchAllData = true;

        // handleGetAllData("sync");
      }
    }
  }

  $: {
    if ($chain === "ALL") {
      if ($tab === "token") {
        loading =
          $queryAllTokenHolding &&
          $queryAllTokenHolding.some((item) => item.isFetching === true) &&
          $queryTokenHolding.isFetching &&
          $queryOverview.isFetching;
      }
      if ($tab === "nft") {
        loading =
          $queryAllNftHolding &&
          $queryAllNftHolding.some((item) => item.isFetching === true) &&
          $queryNftHolding.isFetching &&
          $queryOverview.isFetching;
      }
    } else {
      if ($tab === "token") {
        loading =
          !isErrorAllData &&
          $queryTokenHolding.isFetching &&
          $queryOverview.isFetching;
      }
      if ($tab === "nft") {
        loading =
          !isErrorAllData &&
          $queryNftHolding.isFetching &&
          $queryOverview.isFetching;
      }
    }
  }

  $: {
    if (
      !localStorage.getItem("view-portfolio-tour") &&
      ($chain === "ALL"
        ? $queryAllTokenHolding.every((item) => item.isFetched) &&
          $queryOverview.isFetched
        : $queryTokenHolding.isFetched && $queryOverview.isFetched)
    ) {
      triggerDrivePortfolio(portfolioStepTours)?.drive();
      localStorage.setItem("view-portfolio-tour", "true");
    }
  }

  $: {
    if (
      $triggerUpdateBundle &&
      !$queryValidate.isFetching &&
      !$queryValidate.isError
    ) {
      queryCache.clear();
      queryClient.invalidateQueries(["overview"]);
      queryClient.invalidateQueries(["vaults"]);
      queryClient.refetchQueries(["nft-holding"]);
      if ($chain !== "ALL") {
        queryClient?.refetchQueries(["token-holding-each-chain"]);
      } else {
        queryClient.refetchQueries(["token-holding"]);
      }
      queryClient.refetchQueries(["airdrops"]);
      queryClient.refetchQueries(["points"]);
      queryClient.refetchQueries(["positions"]);
      // handleGetAllData("reload");
      enabledFetchAllData = true;
      triggerUpdateBundle.update((n) => (n = false));
    }
  }
</script>

<SvelteSeo
  description="Multiple platforms portfolio tracking with metrics tailored for investors.
  Track your tokens, NFTs, DeFi positions, airdrops, and more."
  jsonLd={{
    "@context": "https://schema.org",
    "@type": "WebSite",
    name: "Nimbus",
    url: "https://app.getnimbus.io/",
    potentialAction: {
      "@type": "SearchAction",
      target: {
        "@type": "EntryPoint",
        urlTemplate:
          "https://app.getnimbus.io/?referrer=google-search&tab=token&chain=ALL&address={search_term_string}",
      },
      "query-input": "required name=search_term_string",
    },
  }}
/>

<AddressManagement title="Overview">
  <span slot="reload">
    <div class="flex items-end gap-2">
      <div
        class:loading
        on:click={() => {
          // handleGetAllData("reload");
          mixpanel.track("user_reload");
        }}
      >
        <img src={Reload} alt="" class="w-3 h-3" />
      </div>
      <div class="font-medium text-white text-xs mb-[2px]">
        {#if loading}
          Updating data
        {:else}
          Data updated
          {dayjs(dataUpdatedTime).fromNow()}
        {/if}
      </div>
    </div>
  </span>

  <span slot="body">
    <div class="max-w-[1600px] m-auto md:w-[94%] w-full xl:-mt-26 -mt-34">
      {#if isLoadingSync}
        <div
          class="portfolio_container text-xl font-medium flex flex-col gap-5 justify-center items-center rounded-[20px] p-6 h-screen"
        >
          {syncMsg}
          {#if syncMsg !== "Invalid address"}
            {#if isNimbusVersion}
              <Testimonial />
            {:else}
              <LoadingPremium />
            {/if}
          {/if}
        </div>
      {:else}
        <div>
          {#if syncMsg}
            <div
              class="portfolio_container text-xl font-medium flex flex-col gap-5 justify-center items-center rounded-[20px] p-6 h-screen"
            >
              {syncMsg}
              {#if syncMsg !== "Invalid address"}
                {#if isNimbusVersion}
                  <Testimonial />
                {:else}
                  <LoadingPremium />
                {/if}
              {/if}
            </div>
          {:else}
            <div
              class="portfolio_container flex flex-col xl:gap-7 gap-5 rounded-[20px] xl:p-8 p-4"
            >
              <div class="flex flex-wrap items-center xl:gap-2 gap-y-2 gap-x-1">
                <AnimateSharedLayout>
                  {#each $typeWallet === "BUNDLE" || $typeWallet === "EVM" ? typePortfolioPage.filter((item) => item.value !== "history") : typePortfolioPage as type}
                    <div
                      class="relative cursor-pointer text-base font-medium py-1 px-3 rounded-[100px] transition-all"
                      id={type.id}
                      on:click={() => {
                        if ($tab !== type.value) {
                          window.history.replaceState(
                            null,
                            "",
                            window.location.pathname +
                              `?tab=${type.value}&type=${$typeWallet}&chain=${$chain}&address=${$wallet}`,
                          );
                          tab.update((n) => (n = type.value));
                          totalTokens.update((n) => (n = 0));
                          totalAirdrops.update((n) => (n = 0));
                          totalNfts.update((n) => (n = 0));
                          totalPositions.update((n) => (n = 0));
                          unrealizedProfit.update((n) => (n = 0));
                          realizedProfit.update((n) => (n = 0));
                          pastProfit.update((n) => (n = 0));
                          mixpanel.track(`user_select_tab_${type.value}`);
                        }
                      }}
                    >
                      <div
                        class={`relative z-1 ${
                          $tab === type.value && "text-white"
                        }`}
                      >
                        {$t(type.label)}
                      </div>

                      {#if type.value === $tab}
                        <Motion
                          let:motion
                          layoutId="active-pill"
                          transition={{ type: "spring", duration: 0.6 }}
                        >
                          <div
                            class="absolute inset-0 rounded-full bg-[#1E96FC] z-0"
                            use:motion
                          />
                        </Motion>
                      {/if}
                    </div>
                  {/each}
                </AnimateSharedLayout>
              </div>

              {#if $tab === "token" || $tab === "nft"}
                <Overview
                  {enabledFetchAllData}
                  {handleSelectedTableTokenHolding}
                />

                <!-- <Charts
                  {enabledFetchAllData}
                  {handleSelectedTableTokenHolding}
                /> -->

                <Holding
                  {enabledFetchAllData}
                  {selectedTokenHolding}
                  {selectedDataPieChart}
                />
              {/if}

              {#if $tab === "defi"}
                <Defi />
              {/if}

              {#if $tab === "airdrops"}
                <AirdropsAndPointsTracker />
              {/if}

              {#if $tab === "summary"}
                <PerformanceSummary />
              {/if}

              {#if $tab === "history"}
                <History />
              {/if}
            </div>
          {/if}
        </div>
      {/if}
    </div>
  </span>
</AddressManagement>

<style>
  .loading {
    cursor: pointer;
    animation-name: loadingAnimation;
    animation-duration: 1.4s;
    animation-iteration-count: infinite;
    animation-timing-function: linear;
  }

  @keyframes loadingAnimation {
    0% {
      transform: rotate(0);
    }
    100% {
      transform: rotate(360deg);
    }
  }
  :global(body) .portfolio_container {
    background: #fff;
    box-shadow: 0px 0px 40px 0px rgba(0, 0, 0, 0.1);
  }
  :global(body.dark) .portfolio_container {
    background: #0f0f0f;
    box-shadow: 0px 0px 5px 0px rgba(0, 0, 0, 1);
  }
</style>
