<script lang="ts">
  import { onMount } from "svelte";
  import VirtualList from "svelte-tiny-virtual-list";
  import {
    wallet,
    user,
    selectedBundle,
    realtimePrice,
    isDarkMode,
    typeWallet,
    showScallopDeFiFarmModal,
    selectedTokenDetail,
  } from "~/store";
  import {
    shorterName,
    shorterAddress,
    wait,
    getChangePercent,
  } from "~/utils/index";
  import { triggerToast } from "~/utils/functions";
  import {
    listSupported,
    detectedChain,
    chainList,
    chainMoveList,
    chainSolList,
  } from "~/lib/chains";
  import numeral from "numeral";
  import { Progressbar } from "flowbite-svelte";
  import dayjs from "dayjs";
  import { nimbus } from "~/lib/network";
  import CopyToClipboard from "svelte-copy-to-clipboard";
  import mixpanel from "mixpanel-browser";
  import tooltip from "~/lib/tooltip";
  import { isNimbusVersion } from "~/utils/constants";
  import { t } from "~/lib/i18n";

  import Tooltip from "~/components/Tooltip.svelte";
  import TooltipNumber from "~/components/TooltipNumber.svelte";
  import Button from "~/components/Button.svelte";
  import Copy from "~/components/Copy.svelte";
  import Image from "~/components/Image.svelte";
  import OverlaySidebar from "~/components/OverlaySidebar.svelte";
  import OverlaySidebarSwap from "~/components/OverlaySidebarSwap.svelte";
  import AppOverlay from "~/components/Overlay.svelte";
  import LoadingPremium from "~/components/LoadingPremium.svelte";
  import Loading from "~/components/Loading.svelte";
  import VaultTable from "~/UI/Portfolio/VaultTable.svelte";
  import TokenDetailSidebar from "~/UI/TokenDetail/TokenDetailSidebar.svelte";
  import Table from "~/components/Table.svelte";
  import ScallopLendingStake from "./Defi/ScallopLendingStake.svelte";
  import Swap from "./Swap.svelte";

  import TrendUp from "~/assets/trend-up.svg";
  import TrendDown from "~/assets/trend-down.svg";
  import defaultToken from "~/assets/defaultToken.png";

  export let checkScamToken;
  export let filterTokenType;
  export let sumTokens;
  export let defaultData;
  export let dataTokenHolding;
  export let isLoading = false;
  export let handleUpdateTotalTokenHolding = (data) => {};

  let data = dataTokenHolding;

  let isShowTooltipName = false;
  let isShowTooltipSymbol = false;
  let isShowCMC = false;
  let isShowCoingecko = false;
  let isShowReport = false;
  let isShowSwap = false;
  let isShowTooltipReport = false;

  let showTableVaults = false;
  let selectedVaults: any;
  let selectedVaultsSymbol: any;

  let isShowReportTable = false;
  let isOldToken = false;
  let isLoadingReportTrashCoin = false;
  let reportReasonList = [];

  let showTokenInfoBundle = false;

  let showSideTokenDetail = false;
  let isCopied = false;
  let isShowTooltipContractAddress = false;

  let showSideTokenSwap = false;

  let virtualList: any;
  let listSelectedIndex: any = [];
  let selectedItemIndex = -1;

  let listTrashToken: any = [];

  const listChainSupportedSwap = [
    // "SOL",
    "TON",
    ...chainMoveList
      .filter((item) => item.value !== "ALL")
      .map((item) => item.value),
    ...chainList
      .filter((item) => item.value !== "ALL")
      .map((item) => item.value),
  ];

  const reasonReport = [
    {
      id: "trash",
      content: "This token is trash 🗑️",
    },
    {
      id: "scam",
      content: "This token is the scam 🤬",
    },
    {
      id: "hate",
      content: "I hate this token 😠",
    },
  ];

  const closeSideBar = (event: any) => {
    if (event.key === "Escape") {
      showSideTokenSwap = false;
      showSideTokenDetail = false;
      selectedTokenDetail.update((n) => (n = {}));
    }
  };

  const handleReportTrashCoin = async () => {
    isLoadingReportTrashCoin = true;
    try {
      let reason = "";

      if (document.getElementById("trash").checked) {
        reason += "Trash Token, ";
      }

      if (document.getElementById("hate").checked) {
        reason += "Hate Token, ";
      }

      if (document.getElementById("scam").checked) {
        reason += "Scam Token, ";
      }

      if (document.getElementById("outdated").checked) {
        reason += document.getElementById("reason").value;
      }

      const formData = {
        chain: document.getElementById("chain").value,
        contractAddress: document.getElementById("contract_address").value,
        reason: reason,
        contractName: $selectedTokenDetail?.name,
        contractTickerSymbol: $selectedTokenDetail?.symbol,
        logoUrl: $selectedTokenDetail?.logo,
        type: "token",
      };
      await nimbus.post("/holding/trash/report", formData);
      isLoadingReportTrashCoin = false;
      triggerToast("We will update after 2 minutes.", "success");

      listTrashToken = [
        ...listTrashToken,
        {
          chain: formData.chain,
          ca: formData.contractAddress,
          symbol: formData.contractTickerSymbol,
        },
      ];
      localStorage.setItem("listTrashToken", JSON.stringify(listTrashToken));
    } catch (error) {
      isLoadingReportTrashCoin = false;
      triggerToast(
        "Something wrong when report token. Please try again!",
        "fail"
      );
      console.error("error:", error);
    } finally {
      isShowReportTable = false;
    }
  };

  const handleCalculateValue = (data: any, price: any) => {
    return Number(data?.amount) * Number(price);
  };

  const handleCalculateRatio = (data: any, price: any) => {
    const value = Number(data?.amount) * Number(price);
    return (value / sumTokens) * 100;
  };

  const handleCalculatePnL = (data: any) => {
    const realizedProfit = handleCalculateRealizedProfit(data);
    const unrealizedProfit = handleCalculateUnrealizedProfit(data);

    return {
      pnl: realizedProfit.realizedProfit + unrealizedProfit.unrealizedProfit,
      percentPnL:
        realizedProfit.percentRealizedProfit +
        unrealizedProfit.percentUnrealizedProfit,
    };
  };

  const handleCalculateRealizedProfit = (data: any) => {
    return {
      realizedProfit: data?.profit?.realizedProfit
        ? Number(data?.profit?.realizedProfit)
        : 0,
      percentRealizedProfit:
        Number(data?.avgCost) === 0
          ? 0
          : (data?.profit?.realizedProfit
              ? Number(data?.profit?.realizedProfit)
              : 0) / Math.abs(Number(data?.avgCost)),
    };
  };

  const handleCalculateUnrealizedProfit = (data: any) => {
    const realizedProfit = data?.profit?.realizedProfit
      ? Number(data?.profit?.realizedProfit)
      : 0;

    const pnl =
      Number(data?.profit?.totalBought || 0) >
      Number(data?.profit?.totalSold || 0)
        ? Number(data?.profit?.totalBought || 0) *
            Number(data.market_price || 0) +
          Number(data?.profit?.totalGain || 0) -
          Number(data?.profit?.cost || 0)
        : realizedProfit;

    return {
      unrealizedProfit:
        Number(data?.avgCost) === 0 ? 0 : Number(pnl) - realizedProfit,
      percentUnrealizedProfit:
        Number(data?.avgCost) === 0
          ? 0
          : (Number(data?.avgCost) === 0 ? 0 : Number(pnl) - realizedProfit) /
            Math.abs(Number(data?.avgCost)),
    };
  };

  const handleCheckVault = (data: any) => {
    if (data && data?.vaults && data?.vaults?.length !== 0) {
      return data?.vaults?.reduce(
        (maxObject: any, currentObject: any) => {
          return currentObject.apy > maxObject.apy ? currentObject : maxObject;
        },
        { apy: -Infinity }
      );
    }
    return undefined;
  };

  const handleCheckWithinLast24Hours = (data: any) => {
    return dayjs().diff(dayjs(data?.last_transferred_at), "hour");
  };

  const handleFormatDataBreakdown = (data: any) => {
    return (data?.breakdown || [])
      .map((item: any) => {
        const selectedAddress = $selectedBundle?.accounts?.find(
          (account: any) =>
            account?.id?.toLowerCase() === item?.owner?.toLowerCase() ||
            account?.value?.toLowerCase() === item?.owner?.toLowerCase()
        );
        return {
          ...item,
          logo: selectedAddress?.logo,
          type: selectedAddress?.type,
          label: selectedAddress?.label,
        };
      })
      .filter((item: any) => Number(item?.amount) !== 0)
      .sort((a: any, b: any) => b.amount - a.amount);
  };

  const handleRecomputeHeight = (index: any) => {
    virtualList.recomputeSizes(index);
  };

  $: {
    if (showSideTokenDetail) {
      mixpanel.track("token_detail_page", {
        address: $wallet,
        token_address: $selectedTokenDetail?.contract_address || "",
      });
    }
  }

  $: {
    if (checkScamToken) {
      if (filterTokenType) {
        if (filterTokenType.value === 0) {
          data = dataTokenHolding?.filter((item: any) => !item.is_spam);
        } else {
          data = dataTokenHolding?.filter(
            (item: any) =>
              item?.amount * item.market_price > filterTokenType.value &&
              !item.is_spam
          );
        }
      } else {
        data = dataTokenHolding?.filter((item: any) => !item.is_spam);
      }
    } else {
      if (filterTokenType) {
        if (filterTokenType.value === 0) {
          data = dataTokenHolding;
        } else {
          data = dataTokenHolding?.filter(
            (item: any) =>
              item?.amount * item.market_price > filterTokenType.value
          );
        }
      } else {
        data = dataTokenHolding;
      }
    }
  }

  // check market price and update price real-time
  $: {
    if ($realtimePrice) {
      if (!isLoading) {
        if (data && data.length !== 0) {
          data = data
            ?.map((item: any) => {
              const marketPrice = $realtimePrice[
                item?.cmc_id ||
                  item?.contractAddress ||
                  item?.symbol ||
                  item?.price?.symbol
              ]
                ? Number(
                    $realtimePrice[
                      item?.cmc_id ||
                        item?.contractAddress ||
                        item?.symbol ||
                        item?.price?.symbol
                    ]?.price
                  )
                : Number(item.market_price);

              return {
                ...item,
                market_price: marketPrice,
                value: Number(item?.amount) * Number(marketPrice),
                percent_24h_price_change:
                  item?.last_24h_price && item?.last_24h_price?.price !== 0
                    ? getChangePercent(
                        Number(marketPrice),
                        Number(item?.last_24h_price?.price)
                      )
                    : null,
              };
            })
            ?.sort((a: any, b: any) => {
              if (a.value < b.value) {
                return 1;
              }
              if (a.value > b.value) {
                return -1;
              }
              return 0;
            });

          handleUpdateTotalTokenHolding({
            totalSumTokens: (data || [])?.reduce(
              (prev: any, item: any) => prev + item.value,
              0
            ),
            totalSumRealizedProfit: (data || [])
              ?.map((item: any) => {
                return {
                  realized_profit: item?.profit?.realizedProfit || 0,
                };
              })
              ?.reduce(
                (prev: any, item: any) => prev + Number(item.realized_profit),
                0
              ),
            totalSumUnrealizedProfit: (data || [])
              ?.filter(
                (item: any) =>
                  Number(item?.amount) > 0 && Number(item?.avgCost) !== 0
              )
              ?.map((item: any) => {
                const price = Number(
                  item?.market_price || item?.price?.price || 0
                );
                const realizedProfit = item?.profit?.realizedProfit
                  ? Number(item?.profit?.realizedProfit)
                  : 0;

                const pnl =
                  Number(item?.profit?.totalBought || 0) >
                  Number(item?.profit?.totalSold || 0)
                    ? Number(item?.profit?.totalBought || 0) * price +
                      Number(item?.profit?.totalGain || 0) -
                      Number(item?.profit?.cost || 0)
                    : realizedProfit;
                return {
                  unrealized_profit:
                    Number(item?.avgCost) === 0
                      ? 0
                      : Number(pnl) - realizedProfit,
                };
              })
              ?.reduce(
                (prev: any, item: any) => prev + Number(item.unrealized_profit),
                0
              ),
          });
        } else {
          handleUpdateTotalTokenHolding({
            totalSumTokens: 0,
            totalSumRealizedProfit: 0,
            totalSumUnrealizedProfit: 0,
          });
        }
      }
    }
  }

  $: {
    if (listTrashToken) {
      data = data.filter(
        (item: any) =>
          !listTrashToken.some(
            (trashItem: any) =>
              trashItem.ca.toLowerCase() === item.contractAddress.toLowerCase()
          )
      );
    }
  }

  onMount(() => {
    const listTrashTokenStorage = localStorage.getItem("listTrashToken");
    if (listTrashTokenStorage) {
      listTrashToken = JSON.parse(listTrashTokenStorage);
    }
  });
</script>

<svelte:window on:keydown={closeSideBar} />

<Table>
  <span slot="desktop">
    {#if data && data.length === 0 && !isLoading}
      <div
        class={`bg_f4f5f8 grid ${listSupported.includes($typeWallet) && $typeWallet === "BUNDLE" ? "grid-cols-8" : "grid-cols-7"}`}
      >
        <div class="col-spans-3 pl-3 py-3 rounded-tl-[10px]">
          <div class="text-left text-xs uppercase font-medium">
            {$t("Assets")}
          </div>
        </div>

        <div class="py-3">
          <div class="text-right text-xs uppercase font-medium">
            {$t("Price/24h Change")}
          </div>
        </div>

        <div class="py-3">
          <div class="text-right text-xs uppercase font-medium">
            {$t("Amount")}
          </div>
        </div>

        <div class="py-3">
          <div class="text-right text-xs uppercase font-medium">
            {$t("Value")}
          </div>
        </div>

        <div class="py-3 pr-3">
          <div class="text-right text-xs uppercase font-medium">PnL</div>
        </div>

        {#if listSupported.includes($typeWallet) && $typeWallet === "BUNDLE"}
          <div class="py-3 rounded-tr-[10px]" />
        {/if}
      </div>
      <div
        class={`grid ${listSupported.includes($typeWallet) && $typeWallet === "BUNDLE" ? "grid-cols-8" : "grid-cols-7"}`}
      >
        <div
          class="col-span-full flex justify-center items-center h-[465px] py-3 px-3 text-base text-gray-400"
        >
          {#if defaultData && defaultData.length === 0}
            {$t("Empty")}
          {:else}
            {$t("All tokens less than")} ${filterTokenType.value}
          {/if}
        </div>
      </div>
    {:else}
      <VirtualList
        scrollDirection="vertical"
        width="100%"
        height={data.length < 30 ? data.length * 75 + 1 + 40 : 2250}
        bind:this={virtualList}
        itemCount={data.length + 1}
        itemSize={(index) => {
          if (index > 0) {
            const formatDataBreakdown = handleFormatDataBreakdown(
              data[selectedItemIndex]
            );
            return listSelectedIndex.includes(index - 1)
              ? formatDataBreakdown.length > 12
                ? 500 + 135
                : formatDataBreakdown.length * 75 + 135
              : 75;
          } else {
            return 40;
          }
        }}
        stickyIndices={[0]}
      >
        <div
          class={`grid group transition-all ${index > 0 ? "cursor-pointer" : ""} ${listSupported.includes($typeWallet) && $typeWallet === "BUNDLE" ? "grid-cols-8" : "grid-cols-7"}`}
          slot="item"
          let:index
          let:style
          {style}
          on:mouseover={() => {
            if (index > 0) {
              selectedItemIndex = index - 1;
              isShowSwap = true;
              if ($user && Object.keys($user).length !== 0) {
                isShowReport = true;
              }
              if (data[index - 1]?.cmc_slug) {
                isShowCMC = true;
              }
              if (data[index - 1]?.cg_id) {
                isShowCoingecko = true;
              }
            }
          }}
          on:mouseleave={() => {
            if (index > 0) {
              selectedItemIndex = -1;
              isShowSwap = false;
              if ($user && Object.keys($user).length !== 0) {
                isShowReport = false;
              }
              if (data[index - 1]?.cmc_slug) {
                isShowCMC = false;
              }
              if (data[index - 1]?.cg_id) {
                isShowCoingecko = false;
              }
            }
          }}
          on:click={() => {
            if (index > 0) {
              if (listSupported.includes($typeWallet)) {
                showSideTokenDetail = true;
                selectedTokenDetail.update((n) => (n = data[index - 1]));
              }
            }
          }}
        >
          {#if index === 0}
            <div class="bg_f4f5f8 col-spans-3 pl-3 py-3 rounded-tl-[10px]">
              <div class="text-left text-xs uppercase font-medium">
                {$t("Assets")}
              </div>
            </div>

            <div class="bg_f4f5f8 py-3">
              <div class="text-right text-xs uppercase font-medium">
                {$t("Price/24h Change")}
              </div>
            </div>

            <div class="bg_f4f5f8 py-3">
              <div class="text-right text-xs uppercase font-medium">
                {$t("Amount")}
              </div>
            </div>

            <div class="bg_f4f5f8 py-3">
              <div class="text-right text-xs uppercase font-medium">
                {$t("Value")}
              </div>
            </div>

            <div class="bg_f4f5f8 py-3 pr-3">
              <div class="text-right text-xs uppercase font-medium">PnL</div>
            </div>

            {#if listSupported.includes($typeWallet) && $typeWallet === "BUNDLE"}
              <div class="bg_f4f5f8 py-3 rounded-tr-[10px]" />
            {/if}
          {:else}
            <div
              class={`col-spans-3 pl-3 py-3 ${
                listSelectedIndex.includes(index - 1)
                  ? $isDarkMode
                    ? "bg-[#000]"
                    : "bg-gray-100"
                  : $isDarkMode
                    ? "bg-[#131313] group-hover:bg-[#000]"
                    : "bg-white group-hover:bg-gray-100"
              }`}
              style={`${data.length - 1 === index - 1 ? "border-bottom-left-radius: 10px;" : ""}`}
            >
              <div class="relative flex items-center justify-start gap-3">
                <div class="relative">
                  <div class="rounded-full w-[30px] h-[30px] overflow-hidden">
                    <Image
                      logo={data[index - 1]?.logo}
                      defaultLogo={defaultToken}
                    />
                  </div>
                  {#if ["EVM", "MOVE", "SOL", "BUNDLE"].includes($typeWallet) && data[index - 1]?.chain !== "CEX"}
                    <div class="absolute -top-2 -right-1">
                      <img
                        src={detectedChain(data[index - 1]?.chain)?.logo}
                        alt=""
                        width="15"
                        height="15"
                        class="rounded-full"
                      />
                    </div>
                  {/if}
                </div>

                <div class="flex flex-col gap-1">
                  <div class="flex items-start gap-2">
                    <div
                      class="relative font-medium text-sm"
                      on:mouseover={() => {
                        if (index > 0) {
                          selectedItemIndex = index - 1;
                          isShowTooltipName = true;
                        }
                      }}
                      on:mouseleave={() => {
                        if (index > 0) {
                          selectedItemIndex = -1;
                          isShowTooltipName = false;
                        }
                      }}
                    >
                      {#if !data[index - 1]?.name || data[index - 1]?.name === "N/A"}
                        <span>
                          {shorterAddress(data[index - 1]?.contractAddress)}
                        </span>
                      {:else}
                        <span>
                          {#if data[index - 1]?.name?.length > 10}
                            {shorterName(data[index - 1]?.name, 10)}
                          {:else}
                            {data[index - 1]?.name}
                          {/if}
                        </span>
                      {/if}

                      {#if isShowTooltipName && selectedItemIndex === index - 1 && data[index - 1]?.name?.length > 12}
                        <div
                          class="absolute left-0 -top-8"
                          style="z-index: 2147483648;"
                        >
                          <Tooltip text={data[index - 1].name} />
                        </div>
                      {/if}
                    </div>

                    {#if handleCheckVault(data[index - 1]) !== undefined && isNimbusVersion}
                      <div
                        class="w-max flex items-center justyfy-center px-2 py-1 text_27326F text-[10px] font-medium bg-[#1e96fc33] rounded-[1000px] cursor-pointer"
                        on:click={(e) => {
                          if (index > 0) {
                            e.stopPropagation();
                            showTableVaults = true;
                            selectedVaults = data[index - 1].vaults;
                            selectedVaultsSymbol = data[index - 1].symbol;
                            selectedTokenDetail.update(
                              (n) => (n = data[index - 1])
                            );
                          }
                        }}
                      >
                        <div class="flex gap-1">
                          Farm up to
                          {numeral(
                            handleCheckVault(data[index - 1])?.apy * 100
                          ).format("0,0.00")}% APY
                        </div>
                      </div>
                    {/if}
                  </div>

                  <div class="flex items-center gap-2 min-h-[20px]">
                    <div
                      class="relative font-medium text_00000080 text-xs"
                      on:mouseover={() => {
                        if (index > 0) {
                          selectedItemIndex = index - 1;
                          isShowTooltipSymbol = true;
                        }
                      }}
                      on:mouseleave={() => {
                        if (index > 0) {
                          selectedItemIndex = -1;
                          isShowTooltipSymbol = false;
                        }
                      }}
                    >
                      {#if data[index - 1]?.symbol === undefined}
                        N/A
                      {:else}
                        {shorterName(data[index - 1]?.symbol, 12)}
                      {/if}
                      {#if isShowTooltipSymbol && selectedItemIndex === index - 1 && data[index - 1].symbol.length > 12}
                        <div
                          class="absolute left-0 -top-8"
                          style="z-index: 2147483648;"
                        >
                          <Tooltip text={data[index - 1]?.symbol} />
                        </div>
                      {/if}
                    </div>

                    {#if data[index - 1]?.positionType === "ERC_404"}
                      <span
                        class="inline-flex items-center gap-x-1.5 rounded-full bg-yellow-100 px-1 py-0.5 text-[10px] font-medium text-yellow-800"
                      >
                        <svg
                          class="h-1.5 w-1.5 fill-yellow-500"
                          viewBox="0 0 6 6"
                          aria-hidden="true"
                        >
                          <circle cx={3} cy={3} r={3} />
                        </svg>
                        ERC 404
                      </span>
                    {/if}

                    {#if data[index - 1]?.is_spam}
                      <div
                        class="text-[10px] rounded-[30px] px-2 py-1 w-max mt-1"
                        style="background-color: #FF5733; color: #FFD3B6;"
                      >
                        SCAM
                      </div>
                    {/if}

                    {#if isShowReport && selectedItemIndex === index - 1}
                      <div
                        class="relative w-5 cursor-pointer"
                        on:click={(e) => {
                          if (index > 0) {
                            e.stopPropagation();
                            isShowReportTable = true;
                            selectedTokenDetail.update(
                              (n) => (n = data[index - 1])
                            );
                          }
                        }}
                        on:mouseover={() => {
                          if (index > 0) {
                            selectedItemIndex = index - 1;
                            isShowTooltipReport = true;
                          }
                        }}
                        on:mouseleave={() => {
                          if (index > 0) {
                            selectedItemIndex = -1;
                            isShowTooltipReport = false;
                          }
                        }}
                      >
                        <svg
                          xmlns="http://www.w3.org/2000/svg"
                          width="16"
                          height="16"
                          viewBox="0 0 24 24"
                        >
                          <g
                            fill="none"
                            stroke-linecap="round"
                            stroke-linejoin="round"
                            stroke-width="2"
                          >
                            <path d="M0 0h24v24H0z" />
                            <path
                              fill="currentColor"
                              d="M19 4c.852 0 1.297.986.783 1.623l-.076.084L15.915 9.5l3.792 3.793c.603.602.22 1.614-.593 1.701L19 15H6v6a1 1 0 0 1-.883.993L5 22a1 1 0 0 1-.993-.883L4 21V5a1 1 0 0 1 .883-.993L5 4h14z"
