                    <div class="text_00000099">-</div>
                  {:else}
                    <div
                      class={`${
                        data[index - 1].percent_24h_price_change >= 0
                          ? "text-[#00A878]"
                          : "text-red-500"
                      }`}
                    >
                      <TooltipNumber
                        number={Math.abs(
                          data[index - 1].percent_24h_price_change
                        )}
                        type={Math.abs(
                          data[index - 1].percent_24h_price_change
                        ) > 999999 ||
                        Math.abs(data[index - 1].percent_24h_price_change) <
                          0.001
                          ? "balance"
                          : "percent"}
                      />
                      <span>%</span>
                    </div>
                    <img
                      src={data[index - 1].percent_24h_price_change !== 0
                        ? data[index - 1].percent_24h_price_change >= 0
                          ? TrendUp
                          : TrendDown
                        : ""}
                      alt="trend"
                      class="mb-1"
                    />
                  {/if}
                </div>
              </div>
            </div>

            <div
              class={`py-3 ${
                listSelectedIndex.includes(index - 1)
                  ? $isDarkMode
