                  <span
                    use:tooltip={{
                      content: `<tooltip-detail text="Changed ${handleCheckWithinLast24Hours(data[index - 1])} hrs ago" />`,
                      allowHTML: true,
                      placement: "top",
                      interactive: true,
                    }}
                    class="cursor-pointer"
                  >
                    <div class="w-2 h-2 bg-indigo-500 rounded-full" />
