                        if (index > 0) {
                          e.stopPropagation();
                          listSelectedIndex.includes(index - 1)
                            ? (listSelectedIndex = listSelectedIndex.filter(
                                (item) => item !== index - 1
                              ))
                            : (listSelectedIndex = [
                                ...listSelectedIndex,
                                index - 1,
                              ]);
                          selectedItemIndex = index - 1;
                          handleRecomputeHeight(index - 1);
                        }
                      }}
                    >
                      <svg
                        width="12"
                        height="12"
                        viewBox="0 0 12 12"
                        fill="none"
                        xmlns="http://www.w3.org/2000/svg"
