      {$t("Token breakdown")}
    </div>
    <div
      class={`rounded-[10px] overflow-visible overflow-y-auto h-[563px] relative ${
        $isDarkMode ? "bg-[#131313]" : "bg-[#fff] border border_0000000d"
      }`}
    >
      <table
        class={`table-auto xl:w-full w-[700px] ${
