              </div>
            </th>
            <th class="py-3">
              <div class="font-medium text-right uppercase text-sm">
                {$t("Amount")}
              </div>
            </th>
            <th class="py-3 pr-3 rounded-tr-[10px]">
              <div class="font-medium text-right uppercase text-sm">
                {$t("Value")}
