                >
                  {$t("Empty")}
                </div>
              </td>
            </tr>
          {:else}
            {#each handleFormatDataBreakdown($selectedTokenDetail) as item}
              <tr class="transition-all cursor-pointer group">
                <td
                  class={`pl-3 py-3 ${
                    $isDarkMode
                      ? "group-hover:bg-[#000]"
                      : "group-hover:bg-gray-100"
                  }`}
                >
