                    <div class="rounded-full w-[30px] h-[30px] overflow-hidden">
                      <Image logo={data.logo} defaultLogo={defaultToken} />
                    </div>
                    <div class="flex flex-col items-start">
                      <div class="font-medium text-sm text_00000099">
                        {item.label}
                      </div>
                      <div class="text-sm flex justify-end items-center">
                        <Copy
                          address={item?.owner}
                          iconColor={$isDarkMode ? "#fff" : "#000"}
                          color={$isDarkMode ? "#fff" : "#000"}
                          isShorten
                        />
                      </div>
                    </div>
                  </div>
                </td>

                <td
                  class={`py-3 ${
                    $isDarkMode
                      ? "group-hover:bg-[#000]"
                      : "group-hover:bg-gray-100"
                  }`}
                >
                  <div
                    class="text-sm text_00000099 flex flex-col justify-end items-end gap-2"
                  >
                    <div
                      class="flex justify-end items-center gap-1 font-medium text-sm text_00000099"
                    >
                      <TooltipNumber number={item?.amount} type="balance" />
                      {#if $selectedTokenDetail.symbol === undefined}
                        N/A
                      {:else}
                        {$selectedTokenDetail.symbol}
                      {/if}
                    </div>
                    <div
                      class="flex flex-col items-end justify-end gap-1 w-full"
                    >
                      <div class="flex justify-end text-gray-400 text-sm">
                        <TooltipNumber
                          number={Math.abs(
                            item.amount / $selectedTokenDetail.amount
                          ) * 100}
                          type={Math.abs(
                            item.amount / $selectedTokenDetail.amount
                          ) *
                            100 >
                          999999
                            ? "balance"
                            : "percent"}
                        />%
                      </div>
                      <div class="w-3/4 max-w-30">
                        <Progressbar
                          progress={Math.abs(
                            item.amount / $selectedTokenDetail.amount
                          ) * 100}
                          size="h-1"
                        />
                      </div>
                    </div>
                  </div>
                </td>

                <td
                  class={`py-3 pr-3 ${
                    $isDarkMode
                      ? "group-hover:bg-[#000]"
                      : "group-hover:bg-gray-100"
                  }`}
                >
                  <div
                    class="text-sm text_00000099 font-medium flex justify-end items-center"
                  >
                    $<TooltipNumber
                      number={Number(item?.amount) *
                        Number($selectedTokenDetail.market_price)}
                      type="balance"
                    />
                  </div>
                </td>
              </tr>
            {/each}
          {/if}
        </tbody>
      </table>
    </div>
  </div>
</AppOverlay>

