                  <div
                    class="absolute left-0 -top-8"
                    style="z-index: 2147483648;"
                  >
                    <Tooltip text={$selectedTokenDetail?.symbol} />
                  </div>
                {/if}
              </div>
              <CopyToClipboard
                text={$selectedTokenDetail?.contractAddress === "TONCOIN"
